import react, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link, NavLink } from "react-router-dom";
import "../css/Signup.css";
import logo from '../image/logo_ngang_trang.png';

class Signup extends Component {
    constructor(props){
        super(props)
    }

    componentDidMount(){
        this.props.changeNavbarState(false);
    }

    render() { 
        return ( 
            <div className="signup-background-cover signup-flex signup-light">
                <div className="signup-overlay-secondary signup-position-cover"></div>
                <div className="signup-position-z-index signup-width">
                    <NavLink  activeStyle={{color:'#fff'}} to="/AccommodationFinder/home"><img className="signup-logo" src={logo}/></NavLink>
                    <h5 className="signup-heading-line"><span>Sign Up</span></h5>
                    <div className="signup-option-container">
                        <div className="signup-option signup-owner" >
                            <div className="option-content">
                                <h2>Ch tr</h2>
                                <h4>Bn c th ng bi vit, chat vi admin, nhn c nhng d liu thng k v nhu cu thu tr  c nhng chin lc marketing hiu qu</h4>
                            </div>
                            <div className="option-button">
                                <NavLink className="signup-button signup-border-pill signup-text-bold signup-position-z-index" activeStyle={{color:'#fff'}} to="/AccommodationFinder/signupOwner" onClick = {this.props.changeNavbarState}>ng k</NavLink>
                            </div>
                        </div>
                        <div className="center-div">
                            <div className="center-line"></div>
                        </div>
                        <div className="signup-option signup-renter">
                            <div className="option-content">
                                <h2>Ngi thu tr</h2>
                                <h4>Bn c th tm kim nhng tr ph hp nht vi nhu cu ca mnh...</h4>
                            </div>
                            <div className="option-button">
                                <NavLink className="signup-button signup-border-pill signup-text-bold" activeStyle={{color:'#fff'}} to="/AccommodationFinder/signupRenter" onClick = {this.props.changeNavbarState}>ng k</NavLink>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
 
export default Signup;